.user{
    box-shadow: 10px 8px 15px -2px rgba(135,135,135,0.37);
    border: 2px solid white;
    margin-bottom:30px;
    width:600px;
    padding:20px;
    }
    .user h2{
        color:rgb(255, 247, 247)
    }
    .user span
    {
    font-weight:500;
    color:rgb(245, 234, 89);
    }
    .user__bottom
    {
    margin-top:20px;
    }
    .user__delete
    {
    border:none;
    outline:none;
    padding:10px;
    width:100px;
    background-color:rbga(255,0,0,0.818);
    border-radius:8px;
    color:rgb(245, 234, 234);
    background-color: red;
    cursor:pointer;
    }