.add{
  line-height: 50px;
}
.vv{
  background: transparent;
border:1px solid white;
outline:none;
font-size:18px;
color:white;
padding:10px;
width:200px;
margin-top:30px;
border-radius:12px;
cursor:pointer;transition: all 0.5s ease-in-out;
}
.vv:hover{
background: blueviolet;
border:2px solid rgb(250, 246, 37);
outline:none;
font-size:18px;
color:rgb(255, 230, 5);
padding:10px;
width:200px;
margin-top:30px;
border-radius:12px;
cursor:pointer;
transform: scale(0.98);
transition: all 0.5s ease-in-out;
}
.bb{
  width: 30%;
}
.db{
  display: flex;
  justify-content: space-around;
  align-items: center;
}