nav{
    display:flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(0,48,104);
    padding:10px 0;
    
    
}
nav > h1
{
    font-size:30px;
    color:white;
    font-weight:500;font-weight: bold;
}
nav ul{
    display:flex;
    justify-content: space-around;
    align-items: center;
    width:30%;
    font-weight: bold;
   
}
nav ul a{
    font-size:20px;
    font-weight:300;
    color:white;
    text-decoration:none;font-weight: bold;
}
nav ul a:hover{
    font-size:20px;
    font-weight:300;
    color:white;
    text-decoration:none;font-weight: bold;
    transform: scale(0.92);
}