*
{
    margin:0;position: relative;
    padding:0;font-weight: bold;
}
body{
   background-image: url(24.png);
   background-repeat: no-repeat;
   background-size: cover;
  
   
}
.home__title{
    color: white;
    font-size: 40px;
}
.home
{
    
    max-width:1000px;
    margin:0 auto;
    margin-top:90px;
    height:100%;
    
}
.home__container
{
    height:70vh;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
}
.home__formContainer {
    box-shadow:1px 4px 12px -2px rgba(red,);
    background: transparent;
    padding:30px;
    border-radius:10px;
    width:500px;
}
.home__formContainer input{
    border:1px solid #e5e5e5;
    outline:none;
    padding:15px 10px;
    width:80%;
    border-radius:8px;
    margin:10px 0;
    background:transparent;
    color:white;
    font-size:14px;
}
.home__formContainer input::placeholder
{
    color:white;
}
.home__formContainer button{
    background: transparent;
border:1px solid white;
outline:none;
font-size:18px;
color:white;
padding:10px;
width:200px;
margin-top:30px;
border-radius:12px;
cursor:pointer;transition: all 0.5s ease-in-out;
}
.home__formContainer button:hover{
background: blueviolet;
border:2px solid rgb(250, 246, 37);
outline:none;
font-size:18px;
color:rgb(255, 230, 5);
padding:10px;
width:200px;
margin-top:30px;
border-radius:12px;
cursor:pointer;
transform: scale(0.98);
transition: all 0.5s ease-in-out;
}